.events {
    h2 {
        background: var(--gvgr);
        color: var(--ssgr);
        padding: 1rem 5% .5rem;
        border-top-right-radius: 4px;
        border-top-left-radius: 4px;
        margin-bottom: 0;
    }
    ul {
        list-style: none;
        background: var(--white);
        color: var(--black);
        border-bottom-right-radius: 4px;
        border-bottom-left-radius: 4px;
        border-bottom: 2px solid var(--gvgr);
        padding: 0;
        li {
            &.item {
                border-bottom: 2px solid var(--gvgr);
                position: relative;
                &:last-of-type {
                    border-bottom: 0;
                }
                a {
                    display: block;
                    padding: 1.25em 3rem 1.25em 7%;
                    text-decoration: none;
                    background: var(--wgo20a);
                    @media screen and (min-width: 800px) {
                        padding-right: 5rem;
                    }
                    &:hover,
                    &:focus {
                        background: var(--wgolt);
                        color: var(--graydk); //                        text-decoration: none;
                    }

                    .eventdate {
                        color: var(--stgr);
                        font-weight: 500;
                    }
                    .eventitem {
                        .eventcontent {}
                    }
                    &:after {
                        display: flex;
                        content: " ";
                        position: absolute;
                        right: 1rem;
                        top: 2rem; //background: var(--ssgr);
                        width: 2rem;
                        height: 2rem; // border-radius: 50%;
                        //color: var(--white);
                        //align-items: center;
                        //justify-content: center;
                        //font-weight: 600;
                        text-indent: -9999px;
                        background-position: center;
                        background-repeat: no-repeat;
                        background-size: contain;
                        @media screen and (min-width: 800px) {
                            width: 3rem;
                            height: 3rem;
                            top: 1.5rem;
                        }
                    }
                }
                &.academic {
                    a:after {
                        // content: "A";
                        background-image: url(../svg/calendar-icons/a-academic.svg);
                    }
                }


                &.deadline {
                    a:after {
                        content: "B";
                        background-image: url(../svg/calendar-icons/b-deadline.svg);
                    }
                }

                &.student-activity,
                &.hornet-wow-event {
                    a:after {
                        content: " ";
                        background-image: url(../svg/calendar-icons/c-csuslogo.svg);
                    }
                }
            }

            &.ceremony {
                a:after {
                    // content: "D";
                    background-image: url(../svg/calendar-icons/d-ceremony.svg);
                }
            }

            &.meeting,
            &.networking,
            &.open-house {
                a:after {
                    // content: "E";
                    background-image: url(../svg/calendar-icons/e-networking.svg);
                }
            }
            &.conference,
            &.presentation,
            &.lecture,
            &.training,
            &.workshop,
            &.professional-development {
                a:after {
                    // content: "F";
                    background-image: url(../svg/calendar-icons/f-presentation.svg);
                }
            }

            &.concert,
            &.dance,
            &.movie,
            &.play,
            &.photography,
            &.performance,
            &.literature,
            &.exhibit {
                a::after {
                    //content: "G";
                    background-image: url(../svg/calendar-icons/g-performance.svg);
                }
            }

            &.health-wellness,
            &.recreation {
                a::after {
                    // content: "H";
                    background-image: url(../svg/calendar-icons/h-health.svg);
                }
            }
            &.sporting-event {
                a::after {
                    // content: "I";
                    background-image: url(../svg/calendar-icons/i-sports.svg);
                }
            }
        }
    }


    .cal-event-link {
        text-align: right;
    }

}
